import React, { useState } from 'react';
import { WhiteSpace, Picker, InputItem, Steps } from 'antd-mobile';
import { useHistory } from 'react-router-dom';
import { ejuSubject, artScience } from '@/constants/globalDatas';
import PickItem from '@/components/PickItem';
import { Page, PageTitle, OthersItem, PrimaryButton } from '../styles';

const { Step } = Steps;
const Complete: React.FC = () => {
  const [artScienceValue, setArtScienceValue] = useState<string[]>([]); // 学科的值
  const [ejuSubjectValue, setEjuSubjectValue] = useState<string[]>([]); // 科目
  const history = useHistory();
  // 设置下拉选中的值
  const handleSelectPick = (value: string[], type: string): void => {
    console.log(value);
    switch (type) {
      case 'artScience':
        setArtScienceValue(value);
        break;
      case 'ejuSubject':
        setEjuSubjectValue(value);
        break;
      default:
    }
  };
  return (
    <Page>
      <PageTitle>请完善您的信息</PageTitle>
      <WhiteSpace size="xl" />
      <Steps direction="horizontal" current={0}>
        <Step />
        <Step />
        <Step />
      </Steps>
      <WhiteSpace size="xl" />
      <OthersItem>必填信息</OthersItem>
      <WhiteSpace size="xl" />
      <InputItem placeholder="请输入昵称" />
      <WhiteSpace size="lg" />
      <Picker
        data={artScience}
        cols={1}
        value={artScienceValue}
        onOk={(value: string[]): void => handleSelectPick(value, 'artScience')}
      >
        <PickItem placeholder="请选择文理科" />
      </Picker>
      <Picker
        data={ejuSubject}
        cols={1}
        value={ejuSubjectValue}
        onOk={(value: string[]): void => handleSelectPick(value, 'ejuSubject')}
      >
        <PickItem placeholder="请选择考学科目" />
      </Picker>
      <WhiteSpace size="xl" />
      <PrimaryButton
        type="primary"
        onClick={(): void => history.push('/complete-all')}
      >
        下一步
      </PrimaryButton>
    </Page>
  );
};

export default Complete;
